import React from 'react'
import Seperator from '../../../UI/Seperator/Seperator'
import classes from './Achievements.module.css'
const Achievements = () => {
    return (
        <div className={classes.achievements}>
            <Seperator />
            <h1 className={classes['section-title']}>Achievements</h1>
            <div className={classes['achievement-list']}>
                <div className={classes['achievement-img']}>
                    <img src={require('../../../assets/award/award.png')} alt='award image' />
                </div>
                   <div className={classes.list}>
                    <p>
                        <i class="fi-rr-trophy"></i>
                        <span>Talent Pool Scholarship in 5th grade</span>
                    </p>

                    <p>
                        <i class="fi-rr-trophy"></i>
                        <span>Talent Pool Scholarship in JSC</span>
                    </p>

                    <p>
                        <i class="fi-rr-trophy"></i>
                        <span>General Scholarship in SSC</span>
                    </p>
                    <p>
                        <i class="fi-rr-trophy"></i>
                        <span>Completed 7 courses on Coursera</span>
                    </p>
                    <div className={classes.courses}>
                        <p>
                            
                            <ul className={classes['course-list']}>
                                <li>
                                    <i class="fi-rr-badge"></i>
                                    <a href='https://www.coursera.org/account/accomplishments/certificate/HDSNTG4UD8K4'>Introduction to HTML</a>
                                </li>
                                <li>
                                    <i class="fi-rr-badge"></i>
                                    <a href='https://www.coursera.org/account/accomplishments/certificate/RGHDS53UM8EZ'>Introduction to CSS</a>
                                </li>
                                <li>
                                    <i class="fi-rr-badge"></i>
                                    <a href='https://www.coursera.org/account/accomplishments/certificate/2FFY5CERCPYQ'>Interactivity with JavaScript</a>
                                </li>
                                <li>
                                    <i class="fi-rr-badge"></i>
                                    <a href='https://www.coursera.org/account/accomplishments/certificate/ERLCGDECRHQA'>Advanced Styling with Responsive Design</a>
                                </li>
                                <li>
                                    <i class="fi-rr-badge"></i>
                                    <a href='https://www.coursera.org/account/accomplishments/certificate/U23SMYCFTFWB'>Front-End Web Framework: Bootstrap4</a>
                                </li>
                                <li>
                                    <i class="fi-rr-badge"></i>
                                    <a href='https://www.coursera.org/account/accomplishments/certificate/JYLUNR97ARUB'>Frontend Development with React</a>
                                </li>
                                <li>
                                    <i class="fi-rr-badge"></i>
                                    <a href='https://www.coursera.org/account/accomplishments/certificate/AFEWP3VHUV3A'>Introduction to Git and Github</a>
                                </li>
                            </ul>
                        </p>
                    </div>
                   </div>
                
            </div>
        </div>
    )
}

export default Achievements
